@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300;400;700&display=swap');
.App {
  text-align: center;
  background-color: #fff;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.topBar {
  width: 100%;
  height: 50px;
  background-color: #71BF44;
}
.mainContainer{
  width: 100%;
}
.logo{
  width: 133px;
  height: 86px;
}
nav.no-mobile{
  width: 100%;
  height: 89px;
  text-align: left;
  display: inline-block;
  background-color: #71BF44;
}
.no-mobile-container{
  margin: 0 auto;
  width: 90%;
}
nav.no-mobile ul{
  float: right;
  display: flex;
  right: 0px;
  justify-content: space-around;
  width: 45%;
  margin-top: 20px;
}
nav.no-mobile li {
  list-style-type: none;
  font-size: 20px;
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: 400;
  text-transform: uppercase;
  color: #fff;
}
nav.no-mobile li a{
  text-decoration: none;
  color: #fff;
}
nav.no-mobile li a:hover{
  color: #ACD25D;
}
nav.no-mobile li a.active{
  color: #ACD25D;
}
div.active-line{
  width: 100%;
  height: 5px;
  background-color: #ACD25D;
  display: none;
}
a.active div{
  display: block;
}
#menu-bars{
  background-image: url(./images/menu_lines.svg);
  width: 49px;
  height: 24px;
  position: absolute;
  right: 5%;
  top: 13px;
  display: none;
}
.navMobile{
  display: none;
}
.navMobile ul{
  height: 208px;
  display: none;
  position: relative;
  width: 100%;
  margin: 0px;
  padding: 0px;
  background-color: #333231;
}
.navMobile ul.active{
  display: block;
}
.navMobile li{
  list-style-type: none;
  font-size: 23px;
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: 400;
  text-transform: uppercase;
  color: #fff;
  height: 52px;
  line-height: 52px;
}
.navMobile a{
  position: absolute;
  width: 100%;
  left: 0;
  text-align: center;
  color: #fff;
  text-decoration: none;
}
.navMobile a.active{
  background-color: #D1D2D3;
  color: #333231;
}
footer{
  margin-top: 0px;
  height: 60px;
  font-size: 15px;
}
.footer_logo{
  width: 40px;
}

/**************** HOME PAGE **************************/
header{
  height: calc(100vh - 142px);
  min-height: 320px;
  background: rgb(113,191,68);
  background: linear-gradient(180deg, rgba(113,191,68,1) 0%, rgba(57,96,34,1) 100%);
}
#inicio header{
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: -1px;
}
.header-container{
  width: 90%;
  margin: 0 auto;
  display: flex;
  height: 100%;
  align-items: center;
}
.mas-abajo{
  background-image: url(./images/mas-abajo.svg);
  width: 100px;
  height: 41px;
  margin: 0 auto;
  transform: translate(0px, -50px);
  background-size: contain;
  background-repeat: no-repeat;
}
#penta1{
  margin-bottom: 5px;
  width: 760px;
}
header h1, header h3{
  color: #fff;
}
h1{
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: 700;
  font-size: 41px;
  margin: 0;
  color: #333231;
  text-transform: uppercase;
}
h2{
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: 700;
  font-size: 26px;
  color: #333231;
  text-transform: uppercase;
}
h3{
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: 400;
  font-size: 24px;
  margin: 0;
  color: #333231;
}
.columnsContainer{
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  width: 90%;
}
.column{
  text-align: left;
  width: 40%;
}
.column1{
  float: left;
}
.column2{
  float: right;
  position: relative;
}
.column h2{
  margin-top: 40px;
  margin-bottom: 0px;
}
.column p{
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: 400;
  font-size: 25px;
  margin: 10px 0px;
}
#penta2{
  background-image: url(./images/penta2.svg);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  width: 100%;
  height: 306px;
  max-width: 450px;
}
footer{
  background-color: #333231;
  color: #fff;
  height: 120px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 0 5%;
  margin-top: 30px;
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 22px;
  font-weight: 300;
}
.footer_logo{
  width: 65px;
  margin-left: 20px;
}
.mas-abajo:hover{
  filter: invert();
}

@media screen and (max-width: 1200px) {
  nav.no-mobile li {
    font-size: 24px;
  }
  #page-body{
    margin-top: -1px;
  }
  h1{
    font-size: 29px;
  }
  h2{
    font-size: 28px;
  }
  h3{
    font-size: 26px;
    margin: 3px 0;
  }
  .column p{
    font-size: 22px;
  }
}
@media screen and (max-width: 940px) {
  nav.no-mobile li {
    font-size: 20px;
  }
  .logo{
    width: 270px;
    height: 203px;
  }
  h1{
    font-size: 23px;
  }
  h2{
    font-size: 22px;
  }
  h3{
    font-size: 18px;
  }
  .column p{
    font-size: 16px;
  }
}
@media screen and (max-width: 769px) {
  #menu-bars{
    display: block;
  }
  .navMobile{
    display: block;
    background-color: #71BF44;
  }
  nav.no-mobile{
    display: none;
  }
  nav{
    margin-bottom: 0px;
    margin-top: -1px;
    text-align: center;
  }

  nav li {
    font-size: 16px;
  }
  .logo{
    width: 220px;
    height: 165px;
  }
  .mainContainer{
    margin-top: -1px;
  }
  header{
    height: calc(100vh - 217px);
  }
  .mas-abajo{
    transform: translate(0px, -80px);
  }
  .columnsContainer{
    display: block;
  }
  .column{
    width: 100%;
  }
  .column2{
    margin-top: 20px;
    text-align: center;
  }
  #inicio .linea-divisoria{
    margin-bottom: 30px;
  }
  h1{
    font-size: 30px;
  }
  h2{
    font-size: 23px;
  }
  h3{
    font-size: 22px;
    margin: -1px 0;
  }
  #penta1{
    width: 540px;
  }
  .titulo-modificar{
    flex-flow: column;
  }
  #modificarContainer button, #agregarContainer button{
    display: block;
    margin: 0 auto!important;
  }
  #modificarContainer label{
    position: absolute;
    margin: 0px!important;
    left: 50%;
    transform: translateX(-50%);
    padding-top: 10px;
  }
  .estrofa-textarea{
    font-size: 20px!important;
    margin: 10px 0!important;
  }
}

@media screen and (max-width: 540px) {
  .logo {
    width: 170px;
    height: 110px;
  }
  header {
    height: calc(100vh - 159px);
  }
  .header-container{
    flex-flow: wrap-reverse;
    height: 90%;
  }
  .header-left{
    height: 150px!important;
  }
  #penta1{
    width: 320px;
  }
  h1{
    font-size: 19px;
  }
  h3{
    font-size: 14px;
  }
  .image-header{
    margin-top: 0!important;
  }
  #abajo-inicio{
    margin-top: -80px;
  }
  .mas-abajo{
    transform: translate(0, -45px);
  }
  #engranajes, #agregar, #edit-image{
    width: 180px;
  }
  input[type="text"]{
    max-width: 74vw!important;
  }
  .textarea-container{
    max-width: 98%;
    margin: 0 0 20px 0!important;
  }
  .modal{
    max-width: 80%;
    font-size: 26px;
  }
  #lista-busqueda{
    padding: 20px!important;
    max-width: 88%;
  }
}
@media screen and (max-width: 320px) { 
  header {
    height: calc(100vh - 164px);
  }
  #penta1{
    width: 270px;
  }
  h1{
    font-size: 15px;
  }
  h3{
    font-size: 10px;
  }
  input[type="text"] {
    max-width: 72vw!important;
}
}
/********************************************************/
/****************** Crear Page **************************/

.header-left{
  width: 50%;
  height: 190px;
  text-align: left;
  background-image: url(./images/crear_header.svg);
  background-size: contain;
  background-repeat: no-repeat;
  display: inline-block;
}
#modificarContainer .header-left{
  background-image: url(./images/modificar_hero.svg);
}
#agregarContainer .header-left{
  background-image: url(./images/agregar_hero.svg);
}
.header-right{
  width: 50%;
  text-align: right;
  display: inline-block;
}
header.two-columns{
  /* display: flex;
  align-items: center; */
}
.image-header{
  width: 300px;
  margin-top: 0px;
}

@media screen and (max-width: 940px) {
  .image-header{
    width: 70%;
    margin-top: 30px;
  }
}

@media screen and (max-width: 769px) {
  .header-left, .header-right{
    width: 100%;
    text-align: center;
  }
  header.two-columns{
    display: block;
  }
  .image-header{
    width: 220px;
    margin-top: 30px;
  }
}

/********************************************************/
/****************** Agregar Page **************************/

.linea-divisoria{
  background-color: #333231;
  width: 100%;
  height: 3px;
}
#lista-cantos{
  margin-top: 30px;
}
#presentacion-cantos{
  margin-top: 48px;
  min-height: 559px;
  border: 7px solid #333231;
}
input[type="text"] {
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 17px;
  font-weight: 300;
  height: 45px;
  width: 266px;
  border-radius: 0 20px 20px 0;
  border-width: 1px;
  border-color: transparent;
  border-left: none;
  max-width: 75vw;
}
#crearContainer button{
  width: 50px;
  height: 49px;
  position: relative;
  top: -6px;
  background-color: #fff;
  border: 1px solid transparent;
  border-right: none;
  border-radius: 20px 0 0 20px;
}
#buscar-container{
  width: 355px;
  position: relative;
  box-shadow: 0px 5px 5px -6px;
  height: 49px;
  border-radius: 20px;
  margin-bottom: 15px;
  max-width: 90vw;
  border: 2px solid #707070;
}
#borrar-termino{
  position: absolute;
  right: 15px;
  top: 16px;
  width: 16px;
  height: 16px;
  display: none;
}
#crearContainer form{
  margin-top: 30px;
}
.titulo-container{
  display: flex;
  align-items: center;
  width: 100%;
  height: 34px;
  justify-content: space-between;
  border: 1px solid #333231;
  margin: -1px;
}
.titulo-container:hover{
  background-color: rgba(113,191,68,0.2);
}
h4{
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: 300;
  font-size: 24px;
  white-space: nowrap;
  text-overflow: ellipsis;
  max-width: 65vw;
  overflow: hidden;
}
.iconos{
  width: 75px;
  display: flex;
  justify-content: space-between;
  padding: 0 3px;
  align-items: center;
}
.icono{
  text-align: center;
  text-transform: uppercase;
}
.icono:hover{
  opacity: 0.5;
  cursor: pointer;
}
.icono-ver{
  background-image: url(./images/icono_ver.svg);
  width: 35px;
  height: 18px;
  background-size: contain;
}
.icono-agregar{
  background-image: url(./images/icono_agregar.svg);
  width: 23px;
  height: 23px;
  background-size: contain;
}
.icono-cerrar{
  background-image: url(./images/icono_cerrar.svg);
  width: 23px;
  height: 23px;
  background-size: contain;
  filter: invert(1);
}
.icono-buscar{
  background-image: url(./images/icono_buscar.svg);
  width: 25px;
  height: 25px;
  background-size: contain;
  position: absolute;
  top: 11px;
  left: 7px;
}
.icono-crear{
  background-image: url(./images/icono_crear.svg);
  width: 65px;
  height: 65px;
  background-size: contain;
  display: block;
}
.icono-descargar{
  background-image: url(./images/icono_descargar.svg);
  width: 72px;
  height: 65px;
  background-size: contain;
  display: block;
  margin: 0 auto;
}
.icono-borrar-todo{
  background-image: url(./images/icono_borrar_todo.svg);
  width: 59px;
  height: 65px;
  background-size: contain;
  display: block;
  margin: 0 auto;
}
.icono-arriba{
  background-image: url(./images/icono_arriba.svg);
  width: 19px;
  height: 23px;
  background-size: contain;
}
.icono-abajo{
  background-image: url(./images/icono_abajo.svg);
  width: 19px;
  height: 23px;
  background-size: contain;
}
.icono-salvar{
  background-image: url(./images/icono-salvar.svg);
  width: 61px;
  height: 63px;
  background-size: contain;
  display: block;
  background-repeat: no-repeat;
  margin: 0 auto;
  right: 0;
  top: 0;
}
.icono-nueva{
  background-image: url(./images/nueva_estrofa.svg);
  width: 61px;
  height: 63px;
  background-size: contain;
  display: block;
  background-repeat: no-repeat;
  margin: 0 auto;
  right: 0;
  top: 0;
}
.icono-eliminar{
  background-image: url(./images/eliminar_himno.svg);
  width: 61px;
  height: 63px;
  background-size: contain;
  display: block;
  background-repeat: no-repeat;
  margin: 0 auto;
  right: 0;
  top: 0;
}
.preview-container{
  position: fixed;
  top: 0px;
  left: 50%;
  background: #fff;
  height: 90vh;
  margin: 20px 20px;
  padding: 20px;
  overflow: auto;
  transform: translate(-50%, 0px);
}
@media screen and (max-width: 769px) {
  .preview-container{
    position: fixed;
    top: 0px;
    left: 0;
    background: #fff;
    height: 92%;
    width: 90%;
    padding: 4%;
    overflow: auto;
    transform: translate(0px, 0px);
    margin: 3% 1%;
  }
}
#himno-overlay{
  background: rgba(0, 0, 0, 0.3);
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  display: none;
}
.estrofa{
  font-size: 20px;
  font-family: 'Roboto Condensed';
  font-weight: 400;
  width: 100%;
  margin: 0 auto;
  padding: 20px 0;
}
.visible{
  display: block!important;
}
#close-preview{
  position: absolute;
  right: 5px;
  top: 5px;
  width: 20px;
  height: 20px;
}
:focus-visible{
  outline: none;
}
#iconos-presentacion{
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
}
.oracion{
  background-color: #71BF44;
  color: #fff;
}
.imagen{
  background-color: #ACD25D;
  color: #fff;
}
.titulo-selected{
  background-color: #D1D2D3;
  color: #FFFFFF;
}
.icono-selected{
  opacity: 0.5;
}
.iconos-ele{
  width: 25px;
  display: flex;
  justify-content: space-between;
  padding: 0 3px;
  align-items: center;
}
.titulo h4{
  margin: 0 0 0 5px;
}
#lista-elementos{
  margin-top: 0px;
  margin-left: 0px;
  background-color: rgba(255,255,255,0.9);;
  z-index: 7;
  position: absolute;
  width: 200px;
  display: none;
}
#paginas{
  display: flex;
  justify-content: center;
  margin: 15px 0;
}
#paginas h3{
  min-width: 130px;
  text-align: center;
}
#flecha-siguiente .icono-abajo{
  transform: rotate(-90deg);
  display: block;
  margin: 0px 50px;
}
#flecha-anterior .icono-abajo{
  transform: rotate(90deg);
  display: block;
  margin: 0px 50px;
}
#flecha-siguiente .icono-abajo:hover{
  opacity: 0.5;
}
#flecha-anterior .icono-abajo:hover{
  opacity: 0.5;
}
#modificarContainer .buscar-container, #agregarContainer .buscar-container{
  width: 355px;
  position: relative;
  box-shadow: 0px 5px 5px -6px;
  height: 49px;
  border-radius: 0 20px 20px 0;
  max-width: 90vw;
  border: 2px solid #707070;
}
#modificarContainer input[type="text"], #agregarContainer input[type="text"] {
  width: 350px;
}
#modificarContainer button, #agregarContainer button{
  background-color: #80B041;
  color: #FFF;
  border: 1px solid #707070;
  border-radius: 20px;
  height: 49px;
  width: 160px;
  font-size: 24px;
  font-family: 'Roboto Condensed';
  font-weight: 400;
  margin-left: 90px;
  cursor: pointer;
}
#modificarContainer label{
  margin-left: 20px;
}
#modificarContainer .column2 .cantidad-header, #agregarContainer .column2 .cantidad-header{
  margin-top: 20px;
}
.titulo-modificar{
  margin: 20px 0;
  display: flex;
  align-items: center;
}
.titulo-modificar h2 {
  margin: 0 5px 0 0;
}
.overlay{
  width: 100vw;
  height: 100vh;
  background-color: rgba(0,0,0,0.5);
  position: fixed;
  top: 0;
  left: 0;
  display: none;
}
#lista-busqueda{
  background-color: #fff;
  position: relative;
  width: 350px;
  top: 50px;
  left: 50%;
  transform: translate(-50%, 0);
  padding: 40px;
  border: 1px solid #333231;
  max-height: calc(100vh - 110px);
  overflow-y: scroll;
}
#lista-busqueda .titulo-container{
  display: flex;
  align-items: center;
  width: 100%;
  height: 34px;
  justify-content: space-between;
  border: 1px solid #333231;
  margin: -1px;
  background-color: #80B041;
  color: #fff;
  cursor: pointer;
}
#lista-busqueda .titulo-container:hover{
  color: #333231;
  background-color: #ACD25D;
}
#lista-busqueda h3{
  margin: 10px 0;
  font-weight: 700;
  font-size: 21px;
}
#lista-busqueda .icono-cerrar{
  position: absolute;
  top: 10px;
  right: 10px;
}
#estrofas-inputs{
  display: flex;
  flex-wrap: wrap;
  margin-top: 40px;
  justify-content: center;
}
#estrofas-inputs h4{
  color: #fff;
  position: absolute;
  top: 0;
  margin: 0;
  left: 50%;
  transform: translate(-50%, -31px);
  font-weight: 400;
}
.estrofa-textarea{
  resize: none;
  font-size: 24px;
  font-family: 'Roboto Condensed';
  font-weight: 400;
  width: 358px;
  /* margin: 0px 20px 20px 0; */
  text-align: center;
  /* border-right: 34px solid #333231; */
  border: none;
}
.loader{
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  display: none;
}
.cantidad-header{
  display: inline-block;
}
.numero{
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: 700;
  font-size: 26px;
  color: #333231;
  display: inline-block;
  width: 30px;
  text-align: right;
  margin-left: 5px;
}
#modificarContainer .linea-divisoria, #agregarContainer .linea-divisoria{
  margin-bottom: 50px;
}
#modificarContainer .column, #agregarContainer .column{
  float: none;
}
#modificarContainer .column2, #agregarContainer .column2{
  text-align: center;
}
.relleno{
  height: 200px;
  line-height: 200px;
}
.textarea-container{
  position: relative;
  display: flex;
  height: 265px;
  align-items: center;
  border: 1px solid;
  border-top: 34px solid #333231;
  margin: 0px 20px 20px 0;
}
.textarea-container .icono-cerrar{
  position: absolute;
  right: 4px;
  top: -29px;
}
.guardar-container{
  width: 91px;
  font-family: 'Roboto Condensed', sans-serif;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
}
.guardar-container:hover{
  opacity: 0.5;
}
#modify-icons, #add-icons{
  display: flex;
  margin-top: 30px;
  justify-content: space-evenly;
}
.modal{
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  width: 320px;
  height: 300px;
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 30px;
  box-shadow: 1px 2px 16px;
  padding: 20px;
  text-align: center;
}
.confirm-modal{
  border: 10px solid red;
}
.confirm-guardar{
  border: 10px solid #80B041;
}
.confirm-guardar span{
  color: #80B041;
}
.confirm-modal span{
  color: red;
}
.modal button:hover{
  opacity: 0.6;
}
.aceptar-btn{
  color: #fff;
  background-color: #80B041;
  width: 100px;
  height: 40px;
  font-size: 20px;
  font-weight: 800;
  bottom: 15px;
  left: 15px;
  position: absolute;
}
.eliminar-btn{
  color: #fff;
  background-color: red;
  width: 100px;
  height: 40px;
  font-size: 20px;
  font-weight: 800;
  bottom: 15px;
  left: 15px;
  position: absolute;
}
.cancelar-btn{
  color: #fff;
  background-color: grey;
  width: 100px;
  height: 40px;
  font-size: 20px;
  font-weight: 800;
  position: absolute;
  bottom: 15px;
  right: 15px;
}